@charset "UTF-8";
/* USER VARIABLES SECTION */
:root {
  --accent: #CC8869;
  --text: #333;
  --regular-text: 16px;
  --lineheight: 1.65;
  --userfont: roboto-st, sans-serif;
  --systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif;
  --padding: 120px;
  --transition: cubic-bezier(.4, 0, 0, 1);
}

/* BOOTSTRAP SETTINGS SECTION */
/* gutter 20px (10px + 10px). Comment this code for default gutter start at 1.5rem (24px) wide. */
.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl {
  --bs-gutter-x: .625rem;
}

.row, .row > * {
  --bs-gutter-x: 1.25rem;
}

/* FONTS LOAD SECTION */
@font-face {
  src: url(/RxJS-learn/static/media/roboto-regular-webfont.280536e0.woff2) format("woff2");
  font-family: "roboto-st";
  font-weight: 400;
  font-style: normal;
}
@font-face {
  src: url(/RxJS-learn/static/media/roboto-italic-webfont.e9c0eebf.woff2) format("woff2");
  font-family: "roboto-st";
  font-weight: 400;
  font-style: italic;
}
@font-face {
  src: url(/RxJS-learn/static/media/roboto-bold-webfont.6d9fb460.woff2) format("woff2");
  font-family: "roboto-st";
  font-weight: 700;
  font-style: normal;
}
@font-face {
  src: url(/RxJS-learn/static/media/roboto-bolditalic-webfont.fb597ee5.woff2) format("woff2");
  font-family: "roboto-st";
  font-weight: 700;
  font-style: italic;
}
@font-face {
  src: url(/RxJS-learn/static/media/assassin.06555bf2.woff2) format("woff2");
  font-family: "assassin-st";
  font-weight: 700;
  font-style: italic;
}
/* GENERAL CSS SETTINGS */
:-ms-input-placeholder {
  color: #666;
}
::placeholder {
  color: #666;
}

::selection {
  background-color: #CC8869;
  background-color: var(--accent);
  color: #fff;
}

input, textarea {
  outline: none;
}

input:focus:required:invalid, textarea:focus:required:invalid {
  border-color: red;
}

input:required:valid, textarea:required:valid {
  border-color: green;
}

body {
  font-family: roboto-st, sans-serif;
  font-family: var(--userfont);
  font-size: 16px;
  font-size: var(--regular-text);
  line-height: 1.65;
  line-height: var(--lineheight);
  color: #333;
  color: var(--text);
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
}

/* USER STYLES */
body, html {
  height: 100%;
}

.showcase {
  height: 100%;
  background-color: #000;
  position: relative;
  color: #fff;
  overflow: hidden;
}

.showcase::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 1;
  box-shadow: inset 0 0 500px #000;
}

.showcase::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(/RxJS-learn/static/media/light.e27483d6.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center -65px;
  animation: k-light 3s ease-in-out infinite;
}

.showcase__content-wrapper {
  position: relative;
  z-index: 2;
  height: 100%;
  padding: 120px 0;
  padding: var(--padding) 0;
}

.showcase__content-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(/RxJS-learn/static/media/glow.2eabcb94.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  animation: k-glow 1.8s ease-in-out infinite;
}

.showcase__header {
  position: absolute;
  text-align: center;
  width: 100%;
  z-index: 1;
  top: 40px;
  font-family: assassin-st, sans-serif;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.75);
}

.showcase__header span {
  color: #CC8869;
  color: var(--accent);
}

.showcase-carousel .swiper-wrapper {
  transition: cubic-bezier(.4, 0, 0, 1);
  transition: var(--transition);
}

.showcase-carousel__item {
  position: relative;
  height: calc(100vh - 120px * 2);
  height: calc(100vh - var(--padding) * 2);
  width: 100%;
  text-align: center;
  opacity: 0.25;
  transform: scale(0.75);
  transition: opacity 1.8s cubic-bezier(.4, 0, 0, 1), transform 1.8s cubic-bezier(.4, 0, 0, 1);
  transition: opacity 1.8s var(--transition), transform 1.8s var(--transition);
}

.showcase-carousel__item::after {
  content: "";
  width: 120px;
  height: 0;
  position: absolute;
  box-shadow: 0 0 45px 10px #010101;
  bottom: 5px;
  left: calc(50% - 60px);
}

.showcase-carousel__item.swiper-slide-active {
  opacity: 0.8;
  transform: scale(0.98);
}

.showcase-carousel__item p {
  position: absolute;
  bottom: -85px;
  width: 100%;
  font-family: assassin-st, sans-serif;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.5);
  text-shadow: #3a4e5e 0 0 10px;
}

.showcase-carousel__image-wrapper > * {
  position: absolute;
  width: 100%;
  height: 100%;
  perspective: 150px;
  transform-style: preserve-3d;
}

.showcase-carousel__image {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.showcase-carousel__image-left {
  perspective-origin: left center;
  -webkit-clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
          clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
}

.showcase-carousel__image-left .showcase-carousel__image {
  animation: k-left-side 2s ease-in-out infinite;
  animation-direction: alternate;
}

.showcase-carousel__image-right {
  perspective-origin: right center;
  -webkit-clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
          clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
  /*       */
  margin-left: -0.55px;
}

.showcase-carousel__image-right .showcase-carousel__image {
  animation: k-right-side 2s ease-in-out infinite;
  animation-direction: alternate;
}

.showcase__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.48;
}

/* NAVIGATION */
.showcase-navigation {
  overflow: hidden;
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.showcase-navigation > * {
  position: absolute;
  height: 100%;
  width: 50%;
  outline: none;
  cursor: pointer;
}

.showcase-navigation > *::before {
  content: "?";
  font-family: assassin-st;
  opacity: 0;
  transition: opacity 0.75s ease-out;
  transform: rotate(-90deg);
  position: absolute;
  left: 50px;
  top: 50%;
  font-size: 45px;
}

.showcase-navigation > *:hover::before {
  opacity: 0.35;
}

.showcase-navigation > *.swiper-button-disabled {
  display: none;
}

.showcase-navigation__next {
  right: 0;
}

.showcase-navigation__next::before {
  transform: rotate(90deg);
  right: 50px;
  left: auto;
}

/* ANIMATIONS */
@keyframes k-light {
  0% {
    opacity: 0.2;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.2;
  }
}
@keyframes k-glow {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0.6;
  }
}
@keyframes k-left-side {
  0% {
    transform: rotateY(-1deg) scaleX(0.92);
  }
  100% {
    transform: rotateY(0deg) scaleX(1);
  }
}
@keyframes k-right-side {
  0% {
    transform: rotateY(0deg) scaleX(1);
  }
  100% {
    transform: rotateY(1deg) scaleX(0.92);
  }
}
