@charset "UTF-8";
/* USER VARIABLES SECTION */
:root {
  --accent: #CC8869;
  --text: #333;
  --regular-text: 16px;
  --lineheight: 1.65;
  --userfont: roboto-st, sans-serif;
  --systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif;
  --padding: 120px;
  --transition: cubic-bezier(.4, 0, 0, 1);
}

/* BOOTSTRAP SETTINGS SECTION */
/* gutter 20px (10px + 10px). Comment this code for default gutter start at 1.5rem (24px) wide. */
.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl {
  --bs-gutter-x: .625rem;
}

.row, .row > * {
  --bs-gutter-x: 1.25rem;
}

/* FONTS LOAD SECTION */
@font-face {
  src: url("../../../fonts/roboto-regular-webfont.woff2") format("woff2");
  font-family: "roboto-st";
  font-weight: 400;
  font-style: normal;
}
@font-face {
  src: url("../../../fonts/roboto-italic-webfont.woff2") format("woff2");
  font-family: "roboto-st";
  font-weight: 400;
  font-style: italic;
}
@font-face {
  src: url("../../../fonts/roboto-bold-webfont.woff2") format("woff2");
  font-family: "roboto-st";
  font-weight: 700;
  font-style: normal;
}
@font-face {
  src: url("../../../fonts/roboto-bolditalic-webfont.woff2") format("woff2");
  font-family: "roboto-st";
  font-weight: 700;
  font-style: italic;
}
@font-face {
  src: url("../../../fonts/assassin.woff2") format("woff2");
  font-family: "assassin-st";
  font-weight: 700;
  font-style: italic;
}
/* GENERAL CSS SETTINGS */
::-webkit-input-placeholder {
  color: #666;
}
::-moz-placeholder {
  color: #666;
}
:-ms-input-placeholder {
  color: #666;
}
::-ms-input-placeholder {
  color: #666;
}
::placeholder {
  color: #666;
}

::-moz-selection {
  background-color: var(--accent);
  color: #fff;
}

::selection {
  background-color: var(--accent);
  color: #fff;
}

input, textarea {
  outline: none;
}

input:focus:required:invalid, textarea:focus:required:invalid {
  border-color: red;
}

input:required:valid, textarea:required:valid {
  border-color: green;
}

body {
  font-family: var(--userfont);
  font-size: var(--regular-text);
  line-height: var(--lineheight);
  color: var(--text);
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
}

/* USER STYLES */
body, html {
  height: 100%;
}

.showcase {
  height: 100%;
  background-color: #000;
  position: relative;
  color: #fff;
  overflow: hidden;
}

.showcase::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 1;
  -webkit-box-shadow: inset 0 0 500px #000;
          box-shadow: inset 0 0 500px #000;
}

.showcase::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(../../../img/images-slider/light.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center -65px;
  -webkit-animation: k-light 3s ease-in-out infinite;
          animation: k-light 3s ease-in-out infinite;
}

.showcase__content-wrapper {
  position: relative;
  z-index: 2;
  height: 100%;
  padding: var(--padding) 0;
}

.showcase__content-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(../../../img/images-slider/glow.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-animation: k-glow 1.8s ease-in-out infinite;
          animation: k-glow 1.8s ease-in-out infinite;
}

.showcase__header {
  position: absolute;
  text-align: center;
  width: 100%;
  z-index: 1;
  top: 40px;
  font-family: assassin-st, sans-serif;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.75);
}

.showcase__header span {
  color: var(--accent);
}

.showcase-carousel .swiper-wrapper {
  -webkit-transition: var(--transition);
  transition: var(--transition);
}

.showcase-carousel__item {
  position: relative;
  height: calc(100vh - var(--padding) * 2);
  width: 100%;
  text-align: center;
  opacity: 0.25;
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
  -webkit-transition: opacity 1.8s var(--transition), -webkit-transform 1.8s var(--transition);
  transition: opacity 1.8s var(--transition), -webkit-transform 1.8s var(--transition);
  transition: opacity 1.8s var(--transition), transform 1.8s var(--transition);
  transition: opacity 1.8s var(--transition), transform 1.8s var(--transition), -webkit-transform 1.8s var(--transition);
}

.showcase-carousel__item::after {
  content: "";
  width: 120px;
  height: 0;
  position: absolute;
  -webkit-box-shadow: 0 0 45px 10px #010101;
          box-shadow: 0 0 45px 10px #010101;
  bottom: 5px;
  left: calc(50% - 60px);
}

.showcase-carousel__item.swiper-slide-active {
  opacity: 0.8;
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.showcase-carousel__item p {
  position: absolute;
  bottom: -85px;
  width: 100%;
  font-family: assassin-st, sans-serif;
  font-size: 32px;
  color: rgba(255, 255, 255, 0.5);
  text-shadow: #3a4e5e 0 0 10px;
}

.showcase-carousel__image-wrapper > * {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-perspective: 150px;
          perspective: 150px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.showcase-carousel__image {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.showcase-carousel__image-left {
  -webkit-perspective-origin: left center;
          perspective-origin: left center;
  -webkit-clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
          clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
}

.showcase-carousel__image-left .showcase-carousel__image {
  -webkit-animation: k-left-side 2s ease-in-out infinite;
          animation: k-left-side 2s ease-in-out infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.showcase-carousel__image-right {
  -webkit-perspective-origin: right center;
          perspective-origin: right center;
  -webkit-clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
          clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
  /*       */
  margin-left: -0.55px;
}

.showcase-carousel__image-right .showcase-carousel__image {
  -webkit-animation: k-right-side 2s ease-in-out infinite;
          animation: k-right-side 2s ease-in-out infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.showcase__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  opacity: 0.48;
}

/* NAVIGATION */
.showcase-navigation {
  overflow: hidden;
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.showcase-navigation > * {
  position: absolute;
  height: 100%;
  width: 50%;
  outline: none;
  cursor: pointer;
}

.showcase-navigation > *::before {
  content: "?";
  font-family: assassin-st;
  opacity: 0;
  -webkit-transition: opacity 0.75s ease-out;
  transition: opacity 0.75s ease-out;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  position: absolute;
  left: 50px;
  top: 50%;
  font-size: 45px;
}

.showcase-navigation > *:hover::before {
  opacity: 0.35;
}

.showcase-navigation > *.swiper-button-disabled {
  display: none;
}

.showcase-navigation__next {
  right: 0;
}

.showcase-navigation__next::before {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  right: 50px;
  left: auto;
}

/* ANIMATIONS */
@-webkit-keyframes k-light {
  0% {
    opacity: 0.2;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.2;
  }
}
@keyframes k-light {
  0% {
    opacity: 0.2;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.2;
  }
}
@-webkit-keyframes k-glow {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0.6;
  }
}
@keyframes k-glow {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0.6;
  }
}
@-webkit-keyframes k-left-side {
  0% {
    -webkit-transform: rotateY(-1deg) scaleX(0.92);
            transform: rotateY(-1deg) scaleX(0.92);
  }
  100% {
    -webkit-transform: rotateY(0deg) scaleX(1);
            transform: rotateY(0deg) scaleX(1);
  }
}
@keyframes k-left-side {
  0% {
    -webkit-transform: rotateY(-1deg) scaleX(0.92);
            transform: rotateY(-1deg) scaleX(0.92);
  }
  100% {
    -webkit-transform: rotateY(0deg) scaleX(1);
            transform: rotateY(0deg) scaleX(1);
  }
}
@-webkit-keyframes k-right-side {
  0% {
    -webkit-transform: rotateY(0deg) scaleX(1);
            transform: rotateY(0deg) scaleX(1);
  }
  100% {
    -webkit-transform: rotateY(1deg) scaleX(0.92);
            transform: rotateY(1deg) scaleX(0.92);
  }
}
@keyframes k-right-side {
  0% {
    -webkit-transform: rotateY(0deg) scaleX(1);
            transform: rotateY(0deg) scaleX(1);
  }
  100% {
    -webkit-transform: rotateY(1deg) scaleX(0.92);
            transform: rotateY(1deg) scaleX(0.92);
  }
}